import { Component} from '@angular/core';
import { DndDropEvent,DropEffect} from 'ngx-drag-drop';
import { field, value } from '../global.model';
import { ActivatedRoute } from '@angular/router';
import swal from 'sweetalert2';

@Component({
  selector: 'app-edit-app',
  templateUrl: './edit-app.component.html',
  styleUrls: ['./edit-app.component.css']
})
export class EditAppComponent {
  columnmode : boolean = false;
  column:number = 3;
  value:value={
    label:"",
    value:""
  };
  success = false;
  fieldModels:Array<field>=[
    {
      "type": "text",
      "name":"Name", 
      "indexFieldName":"Name",    
      "indexField": 1,      
      "placeholder": "Enter placeholder",
      "className": "form-control",      
      "width":"100",
      "newLine":true
    },{
      "type": "select", 
      "name":"Name2", 
      "indexField": 1,  
      "indexFieldName":"Name2",  
      "width":"100", 
      "label": "Select",
      "description": "Select",
      "placeholder": "Select",
      
      "newLine":true,
      "values": [
        {
          "label": "Option 1",
          "value": "option-1"
        },
        {
          "label": "Option 2",
          "value": "option-2"
        },
        {
          "label": "Option 3",
          "value": "option-3"
        }
      ]
    }, {
      "type": "date",
      "icon":"fa-calendar",
      "indexFieldName":"Name3",    
      "indexField": 1,   
      "label": "Date",
      "placeholder": "Date",
      "className": "form-control",
      "width":"100",
      "newLine":true
    },
    {
      "type": "group",
      
      "indexFieldName":"Group",    
      "indexField": 1,   
      "label": "Group",
      "placeholder": "Date",
      "className": "form-control",
      "width":"100",
      "newLine":true,
      "childs":[]
    }];  

  modelFields:Array<field>=[];
  model:any = {
    name:'App name...',
    description:'App Description...',
    theme:{
      bgColor:"ffffff",
      textColor:"555555",
      bannerImage:""
    },
    attributes:this.modelFields
  };
 

  constructor(
    private route:ActivatedRoute
  ) { }


  
    
   onDragged( item:any, list:any[], effect:DropEffect ) {
     
    if( effect === "move" ) {
      const index = list.indexOf( item );
     
           list.splice( index, 1 );
    }
  }
      
  
  
  onDrop( event:DndDropEvent, list?:any[] ) {
  
    if( list && (event.dropEffect === "copy" || event.dropEffect === "move") ) {
      
      if(event.dropEffect === "copy")
      event.data.name = event.data.type+'-'+new Date().getTime();
      event.data.indexField = event.index+1;
      if(this.columnmode)
      {
        if(this.column && event.dropEffect === "copy")
      event.data.width = 100/this.column;
      event.data.newLine =false;
      }
      let index = event.index;
      if( typeof index === "undefined" ) {
        index = list.length;
      }
      
      list.splice( index, 0, event.data );
    }
   
  
  
  }
  onDrop1( event:DndDropEvent, list?:any[] ) {
  
    if( list && (event.dropEffect === "copy" || event.dropEffect === "move") ) {
      
      if(event.dropEffect === "copy")
      event.data.name = event.data.type+'-'+new Date().getTime();
      event.data.indexField = event.index+1;
      if(this.columnmode)
      {
        if(this.column && event.dropEffect === "copy")
      event.data.width = 100/this.column;
      event.data.newLine =false;
      }
      let index = event.index;
      if( typeof index === "undefined" ) {
        index = list.length;
      }
      
      list.splice( index, 0, event.data );
    }
   
  
  
  }

  addValue(values){
    values.push(this.value);
    this.value={label:"",value:""};
  }
  toggleValue(item){
    item.selected = !item.selected;
  }


}
